import React from 'react';
import store from '../../../../store/index.js';
import {removeSearchText, searchInProgress,setDataItems} from '../../../../actions/actionCreators.js';
import {connect} from 'react-redux';

import * as api from '../../../../api/index.js';

import $ from 'jquery';

export class ItemsGallery extends React.Component{
  constructor(props){
    super(props);
    //this.getPhotosAjaxJsonFormat = this.getPhotosAjaxJsonFormat.bind(this);
  }

  componentDidUpdate(prevProps, prevState){
    console.log("ItemsGallery-componentDidUpdate-prevProps",prevProps);
    console.log("ItemsGallery-componentDidUpdate-prevState",prevState);
    console.log("ItemsGallery-componentDidUpdate-this.props",this.props);
    console.log("ItemsGallery-componentDidUpdate-this.state",this.ptate);

      var input ='';
    if(prevProps.inputText !== this.props.inputText
       && prevProps.searchInProgress !== this.props.searchInProgress

       ){
      input = this.props.inputText;
      //var urlflickr = `$https://www.flickr.com/services/rest/?method=flickr.photos.search&api_key=c5566c22791ced6701fffd946e528be3&text=${input}&format=json&nojsoncallback=1`;
      var urlflickr = "https://www.flickr.com/services/rest/?method=flickr.photos.search&api_key=c5566c22791ced6701fffd946e528be3&text="+input+"&extras=+owner_name&format=json&nojsoncallback=1";

      //this.getPhotosAjaxJsonFormat(urlflickr);
      api.getPhotosAjaxJsonFormat(urlflickr);
    }
    if(this.props.inputText === "" && this.props.searchInProgress===true){
      store.dispatch(searchInProgress(false));
    }
      console.log("ItemsGallery-shouldComponentupdate-this.state",this.state)
  }
  // getPhotosAjaxJsonFormat(flickerUrl){
  //   //var self = this;
  //   $.getJSON(flickerUrl)
  //                .done(function (data) {
  //                  console.log('jsonFormat-data',data);
  //                  store.dispatch(setDataItems(data));
  //                  store.dispatch(searchInProgress(false));
  //                  store.dispatch(removeSearchText());
  //                });
  // }

  render(){
    var currentState = store.getState();
    console.log('itemsGallery-render-currentState',currentState);

     var itemsProp = this.props.dataItems;
      console.log('itemsGallery-render-itemsProp',itemsProp);

      var itemsArraySelected= [];

      if(itemsProp !== undefined && Object.prototype.toString.apply(itemsProp)&& !Array.isArray(itemsProp) ){
        itemsArraySelected = itemsProp.photos.photo;
      }
      var itemsArray = [];
      var currentPage = 1;
      if(itemsArraySelected.length>0){
        for(var a=0; a<this.props.selectorDisplay;a++){
            itemsArray.push(itemsArraySelected[a]);
        }
      }


      var photosLoaded  = (itemsArray!== undefined && itemsArray.length>0) ?
                         itemsArray.map((item,index)=>{
                         return <div key={item.id} className="card" >
                                  <div className="item-container" id={item.id} >
                                      <h3 key={item.id} className="item-owner-name"  >{item.ownername}</h3>
                                      <img key={index}  src={"https://farm" + item.farm + ".staticflickr.com/" + item.server + "/" + item.id + '_' + item.secret + ".jpg"} className="photo-image"/>
                                  </div>
                                </div>
                        }) : "Not loaded";
    return(<section className="items-gallery-section">
              <div className="items-gallery-container">
                <div className="items-list">
                {photosLoaded}
                </div>
              </div>
          </section>)
  }
}


function mapStateToProps(state){
  console.log("ItemsGallery-mapStateToProps-state",state);
  return {
    inputText : state.search.text,
    searchInProgress: state.search.inProgress,
    dataItems:state.data.items,
    selectorDisplay:state.selector.onDisplay
  }
}

export default connect(mapStateToProps)(ItemsGallery);



