import initialState from './initialState';

export default function dataItemsReducer(state=initialState.data.items, action){
      switch(action.type){

        case "SET_DATA_ITEMS":
            return { ...state,
              items: action.items
            }

        case "REMOVE_DATA_ITEMS":
            return { ...state,
              items:[]

            }
        default :
        return state;
      }
}



