import React from 'react';
import store from '../../../../store/index.js';
import {setSearchText,searchInProgress, removeDataItems, setIconOnDisplay} from '../../../../actions/actionCreators.js';
import {connect} from 'react-redux';


export class SearchBox extends React.Component{
  constructor(props){
    super(props);
    this.submitForm = this.submitForm.bind(this);
    this.handleInputChange = this.handleInputChange.bind(this);
    this.identifyClickArea = this.identifyClickArea.bind(this);
  }

  componentDidMount(){
      document.addEventListener("mousedown",this.identifyClickArea)
  }
  componentWillUnmount(){
      document.removeEventListener('mousedown', this.identifyClickArea);
  }
  identifyClickArea(event){
    //event.preventDefault();
    event.stopPropagation();
    var eventTargetId = event.target.id;
    if(eventTargetId ==="input-search-box-id"){
      store.dispatch(setIconOnDisplay("cancel"))
      return;
    }
    if(eventTargetId !=="cancel" && eventTargetId !=="search" &&
        eventTargetId !=="2" &&eventTargetId !=="10" &&
        eventTargetId !=="30"
      ){
      store.dispatch(setIconOnDisplay("search"))
      return;
    }
  }

  handleInputChange(event){
    event.preventDefault();
    var currentState = store.getState();
    console.log("SearchBox-handleInputChange-currentState",currentState)
    if(currentState.icon.onDisplay !=="cancel"){
      store.dispatch(setIconOnDisplay("cancel"));
    }



  }
  submitForm(event){
    event.preventDefault();
    console.log("SearchBox-submitForm-event",event);
    console.log("SearchBox-submitForm-event.target",event.target);
    console.log("SearchBox-submitForm-event.target.id",event.target.id);
    var eventId = event.target.id;
      if(eventId === "cancel"){
        console.log("SearchBox-submitForm-insideIF this is cancel");
        this.inputText.value = "";
        store.dispatch(setIconOnDisplay("search"));
        return;
      }
    console.log("SearchBox-submitForm-eventId",eventId);



    var inputTextValue = this.inputText.value;

    console.log("SearchBox-submitForm-inputTextValue",inputTextValue);
    console.log("SearchBox-submitForm-redux-store",store);

    store.dispatch(setSearchText(inputTextValue));
    store.dispatch(searchInProgress(true));
    store.dispatch(removeDataItems());

    var currentState = store.getState();
    console.log("SearchBox-submitForm-currentState",currentState);
    console.log("SearchBox-submitForm-this.props",this.props);
    this.inputText.value = "";
  }
  render(){
    var currentState= store.getState();
      console.log("SearchBox-render-currentState",currentState)
      console.log("SearchBox-render-this.props",this.props)
    var iconTypeSearch = <span id="search"className="search">&#x1F50D;</span>
    var iconTypeCancel =<span id="cancel"className="cancel">&times;</span>
    var iconType = (currentState.icon.onDisplay ==="search")? iconTypeSearch : iconTypeCancel;

    return(<section>
          <form className="form-search-box">
            <input type="text" name="search-box" className="search-box" id="input-search-box-id" ref={(c) => this.inputText = c}  placeholder="search here..." onChange={this.handleInputChange}/>
            <button className="search-button" id={this.props.iconOnDisplay} onClick={this.submitForm} disabled={this.props.searchInProgress}> {iconType}</button>
          </form>
      </section>)
  }
}


function mapStateToProps(state){
  console.log("SearchBox-mapStateToProps-state",state);
  return {
    inputText : state.search.text,
    searchInProgress: state.search.inProgress,
    dataItems: state.data.items,
    iconOnDisplay: state.icon.onDisplay
  }
}
export default connect(mapStateToProps)(SearchBox);



