import initialState from './initialState';


export default function selectorDisplayReducer(state = initialState.selector, action){
      switch(action.type){

        case "SET_SELECTOR_DISPLAY":
          return { ...state,
            onDisplay:action.onDisplay            
          }

        default:
          return state;
      }
}



