 getPhotosAjax(){
   var url = "https://www.flickr.com/services/rest/"
   var method = "flickr.photos.search";
   var apikey = "231916080c8df3c75c7a627242e694a8";
   var text = "sydney";
   var format = "json";
   var nojsoncallback="1";
   var api_sig="939e00964317cf9762623ec30c5ff290";

   // url: `${url}?method=${method}&&api_key=${apikey}&text=${text}$format=${format}&nojsoncallback=${nojsoncallback}&api_sig=${api_sig}`,
       var self = this;
       console.log('IndexStore-getProducts-AJAX Begin');

       $.ajax ({
       method: "GET",
       async: true,
       crossDomain: true,
       url: "https://www.flickr.com/services/rest/?method=flickr.photos.search&api_key=231916080c8df3c75c7a627242e694a8&text=sydney&format=json&nojsoncallback=1",

       contentType: "application/json; charset=utf-8",
        dataType: "json",
       success: function (data) {
           console.log ('AJAX success Data:',data);
           if(self.state.dataLoaded === false){
             self.setState({
               dataLoaded:true,
               infoProducts:data
             })
           }
       },
       failure: function (data) {
               console.log('AJAX Failure Data',data.responseText);
       }, //End of AJAX failure function
       error: function (error) {
               console.log ('AJAX Error Data:',error);
       }
     });
     console.log('IndexStore-getProducts-AJAX END');
 }

getJsonPhotos(url){
  $.getJSON(url , function(data){


    $.each(data.photos.photo, function(i,item){
        var src = "http://farm"+ item.farm +".static.flickr.com/"+ item.server +"/"+ item.id +"_"+ item.secret +"_m.jpg";
        $("<img/>").attr("src", src).appendTo("#images");
        if ( i == 3 ) return false;
    });
});
}

 getPhotosAjaxJsonFormat(flickerUrl){
   var self = this;
   $.getJSON(flickerUrl)
                .done(function (data) {
                  self.setState({
                    photos:data
                  })
                  console.log('jsonFormat-data',data)
                });
 }
 getPhotosUsingFetch(url){
   //====
   fetch(url, {
       method: 'GET',
       headers: {
         'Content-Type': 'application/json',
         'Accept': 'application/json',
         'mode': 'no-cors',
          'tags': 'searchTerm',
          'tagmode': 'any',
          'format': 'json'
       }
     })
     .then(function(response) {
       console.log(response);
       return response.json();
     })
     .then(function(body) {
       console.log(body);
     });
   //=====
 }
